import { useNavigate } from 'react-router-dom';

import EventChoose from '../../sections/ticket/EventChoose';

import Page from '../../components/Page';
import PageBanner from '../../components/wrappers/PageBanner';

export default function SearchEvent() {
    const navigate = useNavigate();
    const onChangeEvent = (selectedEvent) => {
        navigate(`/event/${selectedEvent?._id}/basic`);
    };
    return (
        <Page title="Add Event">
            <PageBanner>
                <div className="container mb-8">
                    <div className="flex justify-center  px-2  w-full">
                        <div className="max-w-lg">
                            <EventChoose mode="buy" handleChangeEvent={onChangeEvent} />
                        </div>
                    </div>
                </div>
            </PageBanner>
        </Page>
    );
}
